.features {
    justify-content: space-evenly;
    align-items: center;
}

.features__list {
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.features__item {
    width: 30%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.features__container {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.features__img {
    display: block;
    width: 50px;
    height: 50px;
    
    background-size: contain;
    background-repeat: no-repeat;

    &--html {
        background-image: url("../img/html-icon.png");
    }

    &--js {
        background-image: url("../img/js-icon.png");
    }

    &--other {
        background-image: url("../img/git-icon-2.png");
    }
}

.features__text {
    width: 100%;
    margin-top: 20px;
    font-size: 24px;
    line-height: 32px;
    text-align: center;
    color: rgba($color: #000000, $alpha: 0.7);
}

.features__certificate {
    margin-top: 20px;
    font-size: 14px;
    line-height: 22px;
    color: rgba($color: #000000, $alpha: 0.7);
    text-decoration: underline;
}

@media (max-width: 660px) {
    .features__list {
        flex-direction: row;
        flex-wrap: wrap;
        align-items: stretch;
        justify-content: space-around;
    }

    .features__item {
        width: 45%;
        margin-bottom: 40px;
    }

    .features__text {
        width: 100%;
        font-size: 14px;
        line-height: 22px;
    }
}