.projects {
    justify-content: flex-start;
    align-items: center;
    overflow: hidden;
}

.projects__controls {
    width: 280px;
    display: flex;
    justify-content: space-between;
    position: absolute;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 3;
}

.projects__prev,
.projects__next {    
    width: 0px;
    height: 0px;
    border: 35px solid transparent;
    border-top: 35px solid #333333;
    background-color: transparent;
    
    cursor: pointer;
    transition: 0.3s ease-in-out;
    outline: none;

    &:hover {
        transition: 0.3s ease-in-out;
        border-top-color: #B22222;
    }

    &:active {
        transition: 0.3s ease-in-out;
    }
}

.projects__prev {
    transform: rotate(180deg) translateY(50%);
}

.projects__list {
    padding-top: 20%;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    overflow: hidden;
}

.projects__item {
    flex-shrink: 0;
    padding: 20px;
    width: 80%;
    display: flex;
    transform: scale(0.8);
    opacity: 0.3;

    border-radius: 6px;
    box-shadow: 0 5px 15px 0 #968f8f;
    background-color: #ffffff;
    z-index: 1;

    &--active {
        transform: scale(1);
        z-index: 2;
        opacity: 1;
    }
}


.projects__img {
    width: 200px;
    height: 200px;

    & img {
        width: 200px;
        height: 200px;
        border-radius: 6px;
    }
}

.projects__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    justify-content: space-between;
    flex-grow: 1;
}

.projects__description {
    text-align: center;
    font-size: 22px;
    line-height: 28px;
    color: rgba($color: #000000, $alpha: 0.7);
}

.projects__buttons {
    display: flex;
}

.projects__button {
    display: block;
    padding: 10px 0;
    width: 150px;
    text-align: center;

    font-size: 14px;
    text-transform: uppercase;
    font-weight: bold;

    border: 2px solid #333333;
    background-color: #ffffff;
    color: #333333;
    border-radius: 6px;
    cursor: pointer;
    transition: 0.3s ease-in-out;

    &:not(:last-child) {
        margin-right: 10px;
    }

    &:hover {
        background-color: #333333;
        color: #ffffff;
        transition: 0.3s ease-in-out;
    }
}

@media (max-width: 890px) {
    .projects__list {
        padding-top: 20vh;
    }

    .projects__item {
        width: 90%;
        flex-direction: column;
        align-items: center;
    }

    .projects__img {
        width: 140px;
        height: 140px;
    
        & img {
            width: 140px;
            height: 140px;
        }
    }

    .projects__description {
        width: 100%;
        font-size: 16px;
        line-height: 22px;
    }

    .projects__buttons {
        margin-top: 20px;
        flex-direction: column;
        align-items: center;
    }

    .projects__button {
        margin-bottom: 10px;   
        &:not(:last-child) {
            margin-right: 0;
        }
    }
}

