.menu {
    position: relative;
    margin-top: 5px;
}

.menu__toggle {
    position: relative;
    width: 26px;
    height: 26px;

    border: none;
    border-top: 4px solid #000000;
    border-bottom: 4px solid #000000;
    background-color: transparent;
    cursor: pointer;
    opacity: 0.7;
    outline: none;
    transition: 0.3s ease-in-out;



    &:hover {
        opacity: 1;
        transition: 0.3s ease-in-out;
    }

    &::after {
        position: absolute;
        content: "";
        top: 7px;
        left: 0;
        height: 4px;
        width: 20px;
        background-color: #000000;
        transition: 0.3s ease-in-out;
    }

    &--active {
        border-color: #B22222;
        opacity: 1;
        transition: 0.3s ease-in-out;

        &::after {
            background-color: #B22222;
            transition: 0.3s ease-in-out;
        }
    }
}

.menu__list {
    position: absolute;
    top: 26px;
    padding-top: 20px;
    width: 200px;
    left: 0;
    display: none;
    flex-direction: column;
    align-items: flex-start;
    background-color: #ffffff;
    

    &--opened {
        display: flex;
    }
}

.menu__item {
    padding: 10px;
    width: 100%;
    margin-bottom: 20px;
    transition: 0.3s ease-in-out;

    &:hover {
        box-shadow: 0px 5px 5px 0 #666666;
        transform: translateY(-2px);
        transition: 0.3s ease-in-out;
    }
    &:active {
        box-shadow: 0px 0px 0px 0 #666666;
        transform: translateY(2px);
        transition: 0.3s ease-in-out;
    }
}

.menu__link {
    display: block;

    font-size: 20px;
    color: #000000;

}