* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

ul {
    list-style: none;
}

a {
    text-decoration: none;
}

body {
    font-family: 'Open Sans', 'Arial', sans-serif;
}



.visually-hidden {
    position: absolute;
    clip: rect(1px 1px 1px 1px);
    padding: 0;
    border: 0;
    height: 1px;
    width: 1px;
    overflow: hidden;
}

.wrapper {
    width: 80%;
    margin: 0 auto;
    max-width: 1024px;
}

@media (max-width: 660px) {
    .wrapper {
        width: 90%;
    }
}