.page-header {
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: column;
}

.page-header__content {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    padding-top: 50px;
    z-index: 2;
}

@media (max-width: 660px) {
    .page-header__content {
        padding-top: 20px;
    }
}