.progress {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
}

.progress__item {
    height: 3px;
    width: 25%;
    background-color: #999999;
    transition: 0.5s ease-in-out;

    &:not(:last-child) {
        margin-right: 5px;
    }

    &--active {
        background-color: #B22222;
        transition: 0.5s ease-in-out;
    }
}