.about {
    justify-content: center;
    align-items: center;
}

.about__text {
    font-size: 22px;
    line-height: 30px;
    width: 30%;
    color: rgba($color: #000000, $alpha: 0.7);
}

.about__photo {
    margin-right: 100px;
}

.about__photo img {
    border-radius: 50%;
}

@media (max-width: 660px) {
    .about {
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .about__text {
        width: 100%;
        margin-top: 40px;
        font-size: 18px;
        line-height: 26px;
        text-align: center;
    }

    .about__photo {
        margin-right: 0;
    }

    .about__photo img {
        width: 200px;
        height: 200px;
    }
}