.contacts {
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.contacts__phone,
.contacts__email {
    font-size: 30px;
    color: #333333;
    font-weight: bold;
    text-decoration: underline;
}

.contacts__email {
    margin-bottom: 20px;
    text-decoration: none;
}

.contacts__messengers {
    margin-top: 20px;
    display: flex;
}

.contacts__messenger {
    display: block;
    width: 50px;
    height: 50px;    
    background-repeat: no-repeat;
    background-size: contain;
    opacity: 0.7;
    transition: 0.3s ease-in-out;

    &:not(:last-child) {
        margin-right: 20px;
    }

    &--tg {
        background-image: url("../img/tg-icon.png");
    }

    &--whatsapp {
        background-image: url("../img/wa-icon.png");
    }

    &:hover {
        opacity: 1;
        transition: 0.3s ease-in-out;
    }
}

@media (max-width: 660px) {
    .contacts__phone,
    .contacts__email {
        font-size: 20px;
    }
}