.main-slider {
    position: relative;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    height: 100vh;
}

.main-slider__slides {
    height: 100%;
}

.main-slider__slide {
    display: none;
    height: 100%;

    &--active {
        display: flex;
    }

    &--touch {
        position: relative;
    }
}

.main-slider__controls {
    display: flex;
    justify-content: space-between;
    width: 100%;
    position: absolute;
    bottom: 20px;
    left: 0;
}

.main-slider__button {
    width: 30px;
    height: 30px;
    background-color: transparent;
    border: none;
    cursor: pointer;
    background-image: url("../img/arrow.png");
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    opacity: 0.7;
    transition: 0.3s ease-in-out;

    &--prev {
        transform: rotate(180deg);
    }

    &:hover {
        opacity: 1;
        transition: 0.3s ease-in-out;
    }
}

@media (max-width: 660px) {
    .main-slider__controls {
        display: none;
    }
}